
.benefits {
  width: $percent-100;
  background-color: $color-light-grey;
  padding: 106px 0;
}

.benefits__content {
  width: 1124px;
  margin: 0 auto;
  height: 1184px;
}

/*-----------------*/
.benefits__first {
  width: 100%;
  height: 370px;
  display: flex;
  background-color: $color-white;
  justify-content: space-between;
  border-radius: 5px;
}

.benefits__first-text {
  width: 382px;
  height: 188px;
  margin: 91px 35px;
}

.benefits__first-title {
  font-family: $font-realway;
  font-size: 36px;
  color: var(--color-black);
  margin-bottom: 30px;
}

.benefits__first-text::after {
  content: "";
  width: 328px;
  height: 4px;
  background-color: $color-redish;
  margin-top: 50px;
  display: block;
}

.benefits__first-description {
  font-family: $font-open-sans;
  font-size: 16px;
  color: var(--color-black);
  line-height: 22px;
}

.benefits__first-macbook {
  width: 386.44px;
  height: 219px;
  margin: 113px 118px 38px 0;
  position: relative;
  z-index: 1;
}

.benefits__first-macbook-img {
  height: 219px;
  width: 386.44px;
}

.benefits__first-text::after {
  content: "";
  width: 328px;
  height: 4px;
  background-color: $color-redish;
  margin-top: 50px;
  display: block;
}

.benefits__first-square {
  width: 180px;
  height: 180px;
  background-color: $color-redish;
  position: absolute;
  display: block;
  border-radius: 41px;
  right: -6px;
  top: -34px;
  z-index: -1;
  transform: rotate(45deg);
}


